  detail:`HUAWEI P smart+ - смартфон середнього класу з безрамковим дизайном, двома основними і двома фронтальними камерами. Одна з головних фішок смартфона полягає у тонких рамках дисплея, який займає майже всю лицьову панель. Матриця виконана за технологією IPS, а її роздільна здатність становить 2340х1080. Діагональ дисплея відносно велика - 6,3 дюйма, але за рахунок тонких рамок смартфон зручно лягає в руку. Смартфон працює під управлінням Android 8, а за продуктивність відповідає процесор Kirin 710 з 8 ядрами і максимальною частотою 2,2 ГГц. Ємність акумулятора становить 3340 мАг, чого при середньому навантаженні вистачає майже на два дні. P smart+ - це відмінне фотографічне рішення завдяки блоку з двох камер, роздільна здатність першої дорівнює 16, а другий - 2 МП. Фронтальна камера також непроста і складається з двох модулів - на 24 і 2 МП. Так що за якість селфі можна не переживати.`,
